<template>
	<view>
		<view class="flex flex-wrap  justify-between">
			<goodsItem v-for="item in goodsList" :key="item.objectId" :list="item"></goodsItem>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				goodsList:[],
				pages:0
			};
		},
		onLoad(){
			this.loadDate()
		},
		// 上拉触底事件
		onReachBottom(){
			this.loadDate()
		},
		// 下拉刷新事件
		onPullDownRefresh(){
			this.pages = 0
			this.goodsList = []
			this.loadDate()
		},
		methods:{
			handleDetail(i){
				console.log(i)
				uni.navigateTo({
					url:'/pages/detail/detail?i=' + i
				})
			},
			// 分页
			loadDate(){
				let skip = this.pages * 10
				let url = `/1.1/classes/goods?where{"bcid":1}&limit=8&skip=${skip}`
				this.$get(url).then((res) => {
					// 把res的results结构出来
					let {results} = res
					// 判断results是否有数据 如果有就改变数据列表
					if(results.length){
						this.pages++
						// 将两个数组的数据合并然后赋给goodsList数组
						this.goodsList = [
							...this.goodsList,
							...res.results
						]
						// 数据加载完关闭下拉刷新
						uni.stopPullDownRefresh()
						return
					} 
					uni.showToast({
						title:'没有咯！'
					})
				})
			}
		}
	}
</script>

